"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Calendar } from "@/components/ui/calendar";
import { Popover, PopoverContent, PopoverTrigger } from "@/components/ui/popover";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer, LineChart, Line } from "recharts";
import { format } from "date-fns";
import { es } from "date-fns/locale";

// Datos iniciales vacíos - el sistema comienza limpio
const salesData = [
  { month: "Enero", sales: 0, quantity: 0, articles: 0 },
  { month: "Febrero", sales: 0, quantity: 0, articles: 0 },
  { month: "Marzo", sales: 0, quantity: 0, articles: 0 },
  { month: "Abril", sales: 0, quantity: 0, articles: 0 },
  { month: "Mayo", sales: 0, quantity: 0, articles: 0 },
  { month: "Junio", sales: 0, quantity: 0, articles: 0 },
  { month: "Julio", sales: 0, quantity: 0, articles: 0 },
  { month: "Agosto", sales: 0, quantity: 0, articles: 0 },
  { month: "Septiembre", sales: 0, quantity: 0, articles: 0 },
  { month: "Octubre", sales: 0, quantity: 0, articles: 0 },
  { month: "Noviembre", sales: 0, quantity: 0, articles: 0 },
  { month: "Diciembre", sales: 0, quantity: 0, articles: 0 }
];

const topProducts: { name: string; quantity: number; revenue: number }[] = [];

export default function DashboardPage() {
  const [startDate, setStartDate] = useState<Date>();
  const [endDate, setEndDate] = useState<Date>();
  const [chartType, setChartType] = useState<"bar" | "line">("bar");
  const [viewType, setViewType] = useState<"sales" | "quantity">("sales");

  const filteredData = salesData; // En una implementación real, filtrarías por fechas

  const totalSales = filteredData.reduce((sum, item) => sum + item.sales, 0);
  const totalQuantity = filteredData.reduce((sum, item) => sum + item.quantity, 0);
  const totalArticles = filteredData.reduce((sum, item) => sum + item.articles, 0);
  const averageSale = totalSales / filteredData.length;

  const formatCurrency = (value: number) => {
    return new Intl.NumberFormat('es-AR', {
      style: 'currency',
      currency: 'ARS'
    }).format(value);
  };

  return (
    <div className="max-w-7xl mx-auto space-y-8">
      {/* Header */}
      <div className="text-center">
        <h1 className="text-3xl font-bold text-gray-900 mb-2">Dashboard de Ventas</h1>
        <p className="text-gray-600">Análisis de ventas mensuales - TIKO MAYORISTA</p>
      </div>

      {/* Filters */}
      <Card>
        <CardHeader>
          <CardTitle>Filtros y Configuración</CardTitle>
          <CardDescription>
            Selecciona el rango de fechas y tipo de visualización
          </CardDescription>
        </CardHeader>
        <CardContent>
          <div className="grid md:grid-cols-4 gap-4">
            {/* Start Date */}
            <div className="space-y-2">
              <Label>Fecha Inicio</Label>
              <Popover>
                <PopoverTrigger asChild>
                  <Button variant="outline" className="w-full justify-start text-left">
                    {startDate ? format(startDate, "dd/MM/yyyy", { locale: es }) : "Seleccionar fecha"}
                  </Button>
                </PopoverTrigger>
                <PopoverContent className="w-auto p-0">
                  <Calendar
                    mode="single"
                    selected={startDate}
                    onSelect={setStartDate}
                    initialFocus
                  />
                </PopoverContent>
              </Popover>
            </div>

            {/* End Date */}
            <div className="space-y-2">
              <Label>Fecha Fin</Label>
              <Popover>
                <PopoverTrigger asChild>
                  <Button variant="outline" className="w-full justify-start text-left">
                    {endDate ? format(endDate, "dd/MM/yyyy", { locale: es }) : "Seleccionar fecha"}
                  </Button>
                </PopoverTrigger>
                <PopoverContent className="w-auto p-0">
                  <Calendar
                    mode="single"
                    selected={endDate}
                    onSelect={setEndDate}
                    initialFocus
                  />
                </PopoverContent>
              </Popover>
            </div>

            {/* Chart Type */}
            <div className="space-y-2">
              <Label>Tipo de Gráfico</Label>
              <Select value={chartType} onValueChange={(value: "bar" | "line") => setChartType(value)}>
                <SelectTrigger>
                  <SelectValue />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="bar">Barras</SelectItem>
                  <SelectItem value="line">Líneas</SelectItem>
                </SelectContent>
              </Select>
            </div>

            {/* View Type */}
            <div className="space-y-2">
              <Label>Vista</Label>
              <Select value={viewType} onValueChange={(value: "sales" | "quantity") => setViewType(value)}>
                <SelectTrigger>
                  <SelectValue />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="sales">Ventas ($)</SelectItem>
                  <SelectItem value="quantity">Cantidad</SelectItem>
                </SelectContent>
              </Select>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* KPI Cards */}
      <div className="grid md:grid-cols-4 gap-6">
        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium text-gray-600">Ventas Totales</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-green-600">
              {formatCurrency(totalSales)}
            </div>
            <p className="text-xs text-gray-500 mt-1">
              Período seleccionado
            </p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium text-gray-600">Cantidad Vendida</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-blue-600">
              {totalQuantity.toLocaleString()}
            </div>
            <p className="text-xs text-gray-500 mt-1">
              Unidades totales
            </p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium text-gray-600">Artículos Diferentes</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-purple-600">
              {totalArticles}
            </div>
            <p className="text-xs text-gray-500 mt-1">
              Productos únicos
            </p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium text-gray-600">Promedio Mensual</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-orange-600">
              {formatCurrency(averageSale)}
            </div>
            <p className="text-xs text-gray-500 mt-1">
              Venta promedio
            </p>
          </CardContent>
        </Card>
      </div>

      {/* Main Chart */}
      <Card>
        <CardHeader>
          <CardTitle>
            {viewType === "sales" ? "Ventas Mensuales" : "Cantidad Vendida por Mes"}
          </CardTitle>
          <CardDescription>
            Evolución {viewType === "sales" ? "de las ventas" : "de la cantidad vendida"} a lo largo del tiempo
          </CardDescription>
        </CardHeader>
        <CardContent>
          <div className="h-80">
            <ResponsiveContainer width="100%" height="100%">
              {chartType === "bar" ? (
                <BarChart data={filteredData}>
                  <CartesianGrid strokeDasharray="3 3" />
                  <XAxis dataKey="month" />
                  <YAxis />
                  <Tooltip 
                    formatter={(value: number) => [
                      viewType === "sales" ? formatCurrency(value) : value.toLocaleString(),
                      viewType === "sales" ? "Ventas" : "Cantidad"
                    ]}
                  />
                  <Legend />
                  <Bar 
                    dataKey={viewType} 
                    fill={viewType === "sales" ? "#10b981" : "#3b82f6"}
                    name={viewType === "sales" ? "Ventas" : "Cantidad"}
                  />
                </BarChart>
              ) : (
                <LineChart data={filteredData}>
                  <CartesianGrid strokeDasharray="3 3" />
                  <XAxis dataKey="month" />
                  <YAxis />
                  <Tooltip 
                    formatter={(value: number) => [
                      viewType === "sales" ? formatCurrency(value) : value.toLocaleString(),
                      viewType === "sales" ? "Ventas" : "Cantidad"
                    ]}
                  />
                  <Legend />
                  <Line 
                    type="monotone" 
                    dataKey={viewType} 
                    stroke={viewType === "sales" ? "#10b981" : "#3b82f6"}
                    strokeWidth={3}
                    name={viewType === "sales" ? "Ventas" : "Cantidad"}
                  />
                </LineChart>
              )}
            </ResponsiveContainer>
          </div>
        </CardContent>
      </Card>

      {/* Top Products */}
      <div className="grid md:grid-cols-2 gap-6">
        <Card>
          <CardHeader>
            <CardTitle>Productos Más Vendidos</CardTitle>
            <CardDescription>Por cantidad de unidades</CardDescription>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              {topProducts.length > 0 ? (
                topProducts.map((product, index) => (
                  <div key={product.name} className="flex items-center justify-between">
                    <div className="flex items-center space-x-3">
                      <div className="w-8 h-8 bg-gray-100 rounded-full flex items-center justify-center text-sm font-medium">
                        {index + 1}
                      </div>
                      <div>
                        <p className="font-medium">{product.name}</p>
                        <p className="text-sm text-gray-500">{product.quantity} unidades</p>
                      </div>
                    </div>
                    <div className="text-right">
                      <p className="font-medium">{formatCurrency(product.revenue)}</p>
                    </div>
                  </div>
                ))
              ) : (
                <div className="text-center py-8 text-gray-500">
                  <p>No hay datos de productos vendidos</p>
                  <p className="text-sm">Los productos aparecerán aquí cuando se registren ventas</p>
                </div>
              )}
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardHeader>
            <CardTitle>Resumen del Período</CardTitle>
            <CardDescription>Estadísticas generales</CardDescription>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              <div className="flex justify-between items-center py-2 border-b">
                <span className="text-gray-600">Mejor mes (ventas)</span>
                <span className="font-medium">Sin datos - {formatCurrency(0)}</span>
              </div>
              <div className="flex justify-between items-center py-2 border-b">
                <span className="text-gray-600">Peor mes (ventas)</span>
                <span className="font-medium">Sin datos - {formatCurrency(0)}</span>
              </div>
              <div className="flex justify-between items-center py-2 border-b">
                <span className="text-gray-600">Crecimiento promedio</span>
                <span className="font-medium text-gray-500">0%</span>
              </div>
              <div className="flex justify-between items-center py-2 border-b">
                <span className="text-gray-600">Total de pedidos</span>
                <span className="font-medium">0</span>
              </div>
              <div className="flex justify-between items-center py-2">
                <span className="text-gray-600">Ticket promedio</span>
                <span className="font-medium">{formatCurrency(0)}</span>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Export Options */}
      <Card>
        <CardHeader>
          <CardTitle>Exportar Datos</CardTitle>
          <CardDescription>
            Descarga los reportes en diferentes formatos
          </CardDescription>
        </CardHeader>
        <CardContent>
          <div className="flex space-x-4">
            <Button variant="outline">
              Exportar a Excel
            </Button>
            <Button variant="outline">
              Exportar a PDF
            </Button>
            <Button variant="outline">
              Imprimir Reporte
            </Button>
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
